@import url("https://fonts.googleapis.com/css2?family=Space+Mono:wght@700&display=swap");

:root {
  --Strong-cyan: hsl(172, 67%, 45%);
  --Very-dark-cyan: hsl(183, 100%, 15%);
  --Custom-cyan: hsl(180, 18%, 40%);
  --Reset-cyan-normally: hsl(183, 79%, 24%);
  --Hover-bg-cyan: hsl(173, 61%, 77%);
  --Dark-grayish-cyan: hsl(186, 14%, 43%);
  --Grayish-cyan: hsl(184, 14%, 56%);
  --Light-grayish-cyan: hsl(185, 41%, 84%);
  --Very-light-rayish-cyan: hsl(189, 41%, 97%);
  --Heading-cyan: hsl(180, 25%, 32%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Space Mono", monospace;
  font-weight: 700;
  background-color: var(--Light-grayish-cyan);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#root {
  max-width: 920px;
  width: 100%;
}

.heading {
  color: var(--Heading-cyan);
  letter-spacing: 8px;
  margin-bottom: 20px;
  padding-top: 285px;
  text-align: center;
  text-transform: uppercase;
}

.heading-span {
  display: block;
}

.common-container {
  width: 100%;
  border-radius: 25px 25px 0 0;
  padding: 32px;
  background-color: white;
}

label {
  display: block;
}

label,
h2 {
  color: var(--Dark-grayish-cyan);
  font-size: 16px;
  margin-bottom: 5px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.margin-tip {
  margin: 20px 0;
}

.input {
  padding: 10px 15px;
  width: 99%;
  text-align: right;
  font-size: 24px;
  font-weight: 700;
  font-family: inherit;
  color: var(--Very-dark-cyan);
  border: none;
  outline: none;
  background-color: var(--Very-light-rayish-cyan);
  border-radius: 5px;
}

input:focus {
  outline: 2px solid var(--Strong-cyan);
  caret-color: var(--Strong-cyan);
}

.relative {
  position: relative;
}

.space-between {
  display: flex;
  justify-content: space-between;
}

.validation {
  color: #e17457;
}

.relative > img {
  position: absolute;
  top: 47.5px;
  left: 15px;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.btn-tip {
  padding: 1rem 3rem;
  font-size: 24px;
  margin-top: 16px;
  background-color: var(--Very-dark-cyan);
  border-radius: 5px;
  border: none;
  color: white;
  cursor: pointer;
  font-family: "Space Mono", monospace;
}

.btn-tip:first-child {
  padding-right: 3.7rem;
}

button:hover {
  background-color: var(--Hover-bg-cyan);
  color: var(--Very-dark-cyan);
}

button:focus {
  background-color: var(--Strong-cyan);
  color: var(--Very-dark-cyan);
}

.custom-input {
  font-size: 24px;
  text-align: right;
  padding: 16px 20px 16px 0;
  margin-top: 16px;
  background-color: var(--Very-light-rayish-cyan);
  border: none;
  outline: none;
  border-radius: 5px;
  font-weight: 700;
  color: var(--Custom-cyan);
}

.result-container {
  border-radius: 15px;
  background-color: var(--Very-dark-cyan);
  padding: 37px 24px 24px;
  margin-top: 25px;
}

.result-container div:first-child {
  margin-bottom: 20px;
}

.paragraph {
  font-size: 16px;
  color: white;
}

.span {
  display: block;
  color: var(--Grayish-cyan);
  font-size: 13px;
}

.paragraph-result {
  font-size: 28px;
  color: var(--Strong-cyan);
}

.reset {
  width: 100%;
  padding: 12px 0;
  margin-top: 33px;
  font-size: 20px;
  text-transform: capitalize;
  color: var(--Very-dark-cyan);
  font-weight: 700;
  background-color: var(--Reset-cyan-normally);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

@media (min-width: 360px) {
  .custom-input {
    width: 139px;
  }
}

@media (min-width: 560px) {
  .heading {
    padding-top: 550px;
  }

  .input {
    width: 100%;
  }
}

@media (min-width: 1024px) {
  .heading {
    padding-top: 0;
  }

  .common-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .calculation-container {
    max-width: 379px;
    width: 100%;
  }

  .btn-tip {
    width: 117px;
    padding: 0.5rem 2.5rem;
  }

  .custom-input {
    padding: 7px 5px 7px 0;
    width: 116px;
  }

  .result-container {
    padding: 40px 40px;
    width: 413px;
  }

  .paragraph-result {
    font-size: 32px;
  }

  .reset {
    margin-top: 132px;
  }
}
